*{
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: 'zombies-reborn';
  src: url('./zombies_reborn-webfont.woff') format('woff2'),
       url('./zombies_reborn-webfont.woff2') format('woff');
  font-weight: normal;
  font-style: normal;

}

#root{
  height: 100vh;
  width: 100vw;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  background: rgba(0, 0, 0, 0.6);
}

.navbar button{
  background: #aaa9;
  color: #350000;
  font-family: 'zombies-reborn';
  text-decoration: none;
  font-size: 3em;
  text-shadow: 3px 3px #ffffff36;
  font-family: 'zombies-reborn';
  font-weight: 500;
  border-radius: 5px;
  width: 84px;
}

a{
  color: #350000;
  font-family: 'zombies-reborn';
  text-decoration: none;
  font-size: 3em;
  text-shadow: 3px 3px #ffffff36;
  font-family: 'zombies-reborn';
  font-weight: 500;

}

.auth-container {
  background:  #350000;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.auth-container > h1 {
  font-size: 8em;
  text-shadow: 5px 5px rgba(245, 245, 245, 0.233);
  margin: 24px;
  color: #000000;
  font-family: 'zombies-reborn';
  font-weight: 500;
}


.auth-container > p {
  margin: 16px;
  text-decoration: underline;
  color: whitesmoke;
}

.auth-container > form {
  border: 1px solid #3339;
  padding: 20px;
  border-radius: 5px;
  background-color: #aaa9;

  display: flex;
  flex-direction: column;
}

.auth-container > form > input {
  margin: 16px 8px;
  padding: 8px;
  border-radius: 4px;
}

.auth-container > form > button {
  margin: 16px 8px;
  padding: 8px;
  border-radius: 4px;
}

.character{
  display: grid;
  grid-auto-flow: column;
  height: 84vh;
  background: #350000;
}



.character h2{
  padding-bottom: 5px;
  text-align: center;
  color: whitesmoke;
}

.character li{
  color: #aaa9;
  text-align: center;
  padding-bottom: 2px;
  font-size: larger;
}

.character-name{
  color: whitesmoke;
  text-align: center;
  background: #350000;
  font-size: 8em;
  text-shadow: 5px 5px rgba(245, 245, 245, 0.233);
  color: whitesmoke;
  font-family: 'zombies-reborn';
  font-weight: 500;
}

.character-picture{
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: relative;
}

.character > form {
  border: 1px solid #3339;
  padding: 20px;
  border-radius: 5px;
  background-color: #aaa9;
  display: flex;
  flex-direction: column;
  height: 25vh;
  width: 25vw;
  margin-left: auto;
  margin-right: auto;
}

.character > form > input {
  margin: 16px 8px;
  padding: 8px;
  border-radius: 4px;
}

.character > form > button {
  margin: 16px 8px;
  padding: 8px;
  border-radius: 4px;
}

.battle{
  height: 84vh;
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  background: #350000;
}

.battle-picture{
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: relative;
}

.battle span{
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
}

.battle span button {
  display: inline-block;
  padding: 5px;
  margin: 15px;
  color: #350000;
  background: whitesmoke;
  border-radius: 5px;
  border: thick;
}

.battle-text{
  color: whitesmoke;
  text-align: center;
}

.monster-name{
  color: whitesmoke;
  text-align: center;
  background: #350000;
  font-size: 8em;
  text-shadow: 5px 5px rgba(245, 245, 245, 0.233);
  color: whitesmoke;
  font-family: 'zombies-reborn';
  font-weight: 500;
}

.store{
  background:#350000;
 height: 84vh; 
 display: grid;
 grid-template-rows: 2fr .5fr ;
}

.store-title{
  color: whitesmoke;
  text-align: center;
  background: #350000;
  font-size: 8em;
  text-shadow: 5px 5px rgba(245, 245, 245, 0.233);
  color: whitesmoke;
  font-family: 'zombies-reborn';
  font-weight: 500;
}

.store-picture{
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 10px;
}

.store-text{
  text-align: center;
  color: whitesmoke;
}

.potions{
  display: grid;
  grid-auto-flow: row;
  justify-content: center;
}

.potions button{
  color: #350000;
}

.potion-picture{
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 100px;
  height: 100px;
}